import Title from "./Title";
import Subtitle from "./Subtitle";
import { CardText } from "./Card";
import { Newest } from "./Posts";
import { BestTools } from "./Tools";
import A from "./A";

const Home = () => {
  return (
    <div>
      <Title title="Rehanaf | Home" />
      <div className="h-screen flex">
        <div className="m-auto">
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" className="w-28 h-28 m-auto">
            <path strokeLinecap="round" strokeLinejoin="round" d="M17.25 6.75L22.5 12l-5.25 5.25m-10.5 0L1.5 12l5.25-5.25m7.5-3l-4.5 16.5" />
          </svg>
          <div className="text-center">
            <h1 className="text-sky-900 text-3xl font-medium p-3 border-b-2 border-slate-600">
              <div className="inline-block" data-aos="fade-down" data-aos-delay={0}>Rehan</div>
              <span> </span>
              <div className="inline-block" data-aos="fade-down" data-aos-delay={200}>Abu</div>
              <span> </span>
              <div className="inline-block" data-aos="fade-down" data-aos-delay={400}>Faqih</div>
            </h1>
            <p className="text-gray-600" data-aos="fade-up" data-aos-delay={500}>Junior Web Developer</p>
          </div>
        </div>
      </div>

    <div className="m-6" data-aos="fade-up">
      <Subtitle name="Skills" />
      <div className="flex justify-center flex-wrap">
        <CardText className="text-orange-400" title="HTML" description="Hyper Text Markup Language" />
        <CardText className="text-sky-400" title="CSS" description="Cascading Style Sheet" />
        <CardText className="text-yellow-400" title="JS" description="Java Script" />
        <CardText className="text-purple-600" title="PHP" description="HyperText Preprocessor" />
        <CardText className="text-sky-400" title="CSS" description="Cascading Style Sheet" />
      </div>
    </div>
    <Newest />
    <div className="flex justify-center">
      <A to="/posts" name="Lihat Lainnya" />
    </div>
    <BestTools />
    <div className="flex justify-center">
      <A to="/tools" name="Lihat Lainnya" />
    </div>
    </div>
  )
}

export default Home;